<template>
  <div class="hello">
    <ul v-bind:class="active" v-on:click.prevent>
      <li><a href="#" class="home" v-on:click="makeActive('home')">Home</a></li>
      <li><a href="#" class="seen" v-on:click="makeActive('seen')">Birds I've Seen</a></li>
      <li><a href="#" class="finder" v-on:click="makeActive('finder')">Bird Finder</a></li>
    </ul>
    <Seen v-if="active === 'seen'"></Seen>
    <Finder v-else-if="active === 'finder'"></Finder>
    <Home v-else></Home>
  </div>
</template>

<script>
import Home from '@/components/Home'
import Seen from '@/components/seen/Seen'
import Finder from '@/components/finder/Finder'

import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    recent: [],
    recentNeeded: [],
    activeHotspot: '',
    hotspotNeeded: [],
    birdsSeen: {
      '067': [
        'Canada Goose', 'Mallard', 'Cooper\'s Hawk', 'Red-shouldered Hawk', 'Red-tailed Hawk', 'Mourning Dove', 'Red-bellied Woodpecker', 'Downy Woodpecker', 'Hairy Woodpecker', 'Eastern Phoebe', 'Blue-headed Vireo', 'Blue Jay', 'American Crow', 'Carolina Chickadee', 'Tufted Titmouse', 'White-breasted Nuthatch', 'Carolina Wren', 'Ruby-crowned Kinglet', 'Eastern Bluebird', 'Hermit Thrush', 'Orange-crowned Warbler', 'Yellow-rumped Warbler', 'Chipping Sparrow', 'White-throated Sparrow', 'Song Sparrow', 'Northern Cardinal', 'American Goldfinch', 'Double-crested Cormorant', 'Great Blue Heron', 'Black Vulture', 'Belted Kingfisher', 'Red-headed Woodpecker', 'Golden-crowned Kinglet', 'Cedar Waxwing', 'hawk sp.', 'crow sp.', 'passerine sp.', 'warbler sp. (Parulidae sp.)', 'Sharp-shinned/Cooper\'s Hawk'
      ],
      '089': [
        'Snow Goose', 'Gadwall', 'Mallard', 'Turkey Vulture', 'Red-shouldered Hawk', 'Belted Kingfisher', 'Red-bellied Woodpecker', 'Pileated Woodpecker', 'Eastern Phoebe', 'Blue Jay', 'Carolina Chickadee', 'Tufted Titmouse', 'White-breasted Nuthatch', 'Carolina Wren', 'Golden-crowned Kinglet', 'Ruby-crowned Kinglet', 'Hermit Thrush', 'American Robin', 'Northern Mockingbird', 'Chipping Sparrow', 'White-throated Sparrow', 'Song Sparrow', 'Eastern Towhee', 'Northern Cardinal', 'Canada Goose', 'Hooded Merganser', 'Great Blue Heron', 'Red-tailed Hawk', 'Mourning Dove', 'Barred Owl', 'Downy Woodpecker', 'Northern Flicker', 'Blue-headed Vireo', 'American Crow', 'Brown-headed Nuthatch', 'Brown Creeper', 'Yellow-rumped Warbler', 'American Goldfinch', 'Wood Duck', 'Pied-billed Grebe', 'Yellow-bellied Sapsucker', 'Eastern Bluebird', 'Cedar Waxwing', 'sparrow sp.', 'Purple Finch', 'Greater White-fronted Goose', 'Muscovy Duck (Domestic type)', 'Pine Warbler', 'House Finch', 'Domestic goose sp. (Domestic type)', 'Killdeer', 'Orange-crowned Warbler', 'Dark-eyed Junco', 'Red-headed Woodpecker', 'Red-winged Blackbird', 'Common Grackle', 'Fish Crow', 'Brown Thrasher', 'Cooper\'s Hawk', 'Winter Wren', 'House Sparrow', 'Black Vulture', 'hawk sp.', 'crow sp.', 'passerine sp.', 'warbler sp. (Parulidae sp.)', 'Sharp-shinned/Cooper\'s Hawk', 'blackbird sp.'
      ],
      '121': [
        'Rock Pigeon (Feral Pigeon)', 'Rock Pigeon', 'Mourning Dove', 'Blue Jay', 'American Crow', 'Carolina Wren', 'American Robin', 'Northern Mockingbird', 'Yellow-rumped Warbler', 'Song Sparrow', 'Northern Cardinal', 'House Sparrow', 'Downy Woodpecker', 'sparrow sp.', 'Muscovy Duck (Domestic type)', 'Mallard', 'Red-shouldered Hawk', 'Red-tailed Hawk', 'Wilson\'s Snipe', 'Eastern Screech-Owl', 'Red-bellied Woodpecker', 'Eastern Phoebe', 'White-breasted Nuthatch', 'Eastern Bluebird', 'Brown Thrasher', 'White-throated Sparrow', 'Eastern Towhee', 'Red-winged Blackbird', 'Common Grackle', 'Yellow-bellied Sapsucker', 'Hairy Woodpecker', 'Carolina Chickadee', 'Tufted Titmouse', 'Brown-headed Nuthatch', 'Golden-crowned Kinglet', 'Ruby-crowned Kinglet', 'Pine Warbler', 'Chipping Sparrow', 'Swamp Sparrow', 'House Finch', 'American Goldfinch', 'Canada Goose', 'Great Blue Heron', 'Belted Kingfisher', 'Northern Flicker', 'Winter Wren', 'European Starling', 'Orange-crowned Warbler', 'Rusty Blackbird', 'blackbird sp.', 'Double-crested Cormorant', 'Osprey', 'Killdeer', 'Spotted Sandpiper', 'Red-headed Woodpecker', 'Black Vulture', 'Cooper\'s Hawk', 'Bald Eagle', 'Hermit Thrush', 'Cedar Waxwing', 'Ring-necked Duck', 'Brown Creeper', 'Dark-eyed Junco', 'Mallard (Domestic type)', 'Turkey Vulture', 'Pileated Woodpecker', 'Fox Sparrow', 'Fish Crow', 'Eurasian Collared-Dove', 'Blue-headed Vireo', 'Field Sparrow', 'Palm Warbler', 'Merlin', 'Blue-winged Teal', 'Northern Pintail', 'hawk sp.', 'crow sp.', 'passerine sp.', 'warbler sp. (Parulidae sp.)', 'Sharp-shinned/Cooper\'s Hawk', 'blackbird sp.'
      ]
    }
  },
  mutations: {
    SET_RECENT: (state, response) => {
      state.recent = Object.assign({}, response.data)
    },
    SET_RECENT_NEEDED: (state, response) => {
      state.recentNeeded = []
      for (var item of response.data) {
        if (!state.birdsSeen[response.countyNumber].includes(item.comName)) {
          state.recentNeeded.push(item)
        }
      }
    },
    SET_HOTSPOT: (state, response) => {
      state.hotspotNeeded = []
      state.activeHotspot = response.hotspot
      for (var item of response.data) {
        if (!state.birdsSeen[response.countyNumber].includes(item.comName)) {
          state.hotspotNeeded.push(item)
        }
      }
    }
  },
  actions: {
    GET_RECENT: (state, countyNumber) => {
      axios.create({
        baseURL: 'http://ebird.org/ws1.1/data/obs/region/recent'
      }).get('?rtype=subnational2&fmt=json&back=7&r=US-GA-' + countyNumber).then(response => {
        state.commit('SET_RECENT', response)
        response.countyNumber = countyNumber
        state.commit('SET_RECENT_NEEDED', response)
      })
    },
    GET_HOTSPOT: (state, params) => {
      axios.create({
        baseURL: 'http://ebird.org/ws1.1/data/obs/hotspot/recent'
      }).get('?fmt=json&back=7&r=' + params.locID).then(response => {
        response.countyNumber = params.countyID
        response.hotspot = params.locName
        state.commit('SET_HOTSPOT', response)
      })
    }
  }
})

export default {
  name: 'big-year',
  components: {
    Home,
    Seen,
    Finder
  },
  store,
  data () {
    return {
      active: 'home'
    }
  },
  methods: {
    makeActive: function (item) {
      this.active = item
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

ul.home .home,
ul.seen .seen,
ul.finder .finder {
  color:#e35885;
}
</style>



