<template>
  <div>
    <h1>Seen</h1>
    <ul v-for='(birds, location) in birdsSeen'>
      <h2>{{ location }}</h2>
      <li v-for='bird in birds'>{{ bird }}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'seen',
  computed: {
    birdsSeen () {
      return this.$store.state.birdsSeen
    }
  }
}

</script>

<style scoped>
